const statsCards = [
  { title: "Total Events" },
  { title: "Active Vendors" },
  { title: "Pending Claims" },
  { title: "Team Members" }
];

const activityItems = [
  "Activity item",
  "Activity item",
  "Activity item",
  "Activity item",
  "Activity item"
];

const quickActions = [
  "Create Event",
  "Add Vendor",
  "File Claim",
  "Upload Files"
];

import { currentUser } from "../lib/auth";
import { hasAccess } from "../lib/rbac";

const sidebarLinks = [
  { label: "My Dashboard", minLevel: 4 },
  { label: "Events", minLevel: 3 },
  { label: "Vendor Management", minLevel: 2 },
  { label: "Expense Claims", minLevel: 3 },
  { label: "Event Uploads", minLevel: 3 },
  { label: "Team", minLevel: 2 }
];

export default function Home() {
  return (
    <div className="dashboard">
      <aside className="sidebar">
        <div className="sidebar-logo">
          <span className="logo-badge">E</span>
        </div>
        <nav className="sidebar-nav">
          {sidebarLinks
            .filter((link) => hasAccess(currentUser.role, link.minLevel))
            .map((link, index) => (
            <button
              key={link.label}
              className={`nav-item hover-text${index === 0 ? " active" : ""}`}
              type="button"
            >
              <span className="nav-icon" aria-hidden="true" />
              <span>{link.label}</span>
            </button>
          ))}
        </nav>
      </aside>

      <div className="main">
        <header className="topbar">
          <div className="brand">
            <span className="logo-badge">E</span>
            <span className="brand-name">ERPSuite</span>
          </div>
          <div className="topbar-actions">
            <button className="icon-button hover-text" type="button" aria-label="Notifications">
              <span className="bell" />
              <span className="status-dot" />
            </button>
            <button className="profile-button hover-text" type="button">
              <span className="avatar">U</span>
              <span className="profile-name">{currentUser.name}</span>
              <span className="chevron" aria-hidden="true" />
            </button>
          </div>
        </header>

        <main className="content">
          <section className="page-header">
            <div>
              <h1>My Dashboard</h1>
              <p>Welcome back. Here&apos;s your overview.</p>
            </div>
          </section>

          <section className="stats-grid">
            {statsCards.map((card) => (
              <div key={card.title} className="stat-card">
                <div className="stat-icon" aria-hidden="true" />
                <div className="stat-content">
                  <div className="stat-value">—</div>
                  <div className="stat-label">{card.title}</div>
                </div>
                <div className="stat-trend">—</div>
              </div>
            ))}
          </section>

          <section className="grid-two">
            <div className="panel">
              <div className="panel-header">
                <h2>Recent Activity</h2>
              </div>
              <div className="panel-body">
                <ul className="activity-list">
                  {activityItems.map((item, index) => (
                    <li key={`${item}-${index}`} className="activity-item">
                      <span className="hover-text">{item}</span>
                      <span className="activity-time">Time</span>
                    </li>
                  ))}
                </ul>
              </div>
            </div>

            <div className="panel">
              <div className="panel-header">
                <h2>Quick Actions</h2>
              </div>
              <div className="panel-body">
                <div className="actions-grid">
                  {quickActions.map((action) => (
                    <button key={action} className="action-card hover-text" type="button">
                      <span className="action-icon" aria-hidden="true" />
                      <span>{action}</span>
                    </button>
                  ))}
                </div>
              </div>
            </div>
          </section>
        </main>

        <footer className="footer">
          <div className="footer-brand">
            <span className="logo-badge">E</span>
            <span>ERPSuite</span>
          </div>
          <div className="footer-contact">
            <span>contact@erpsuite.com</span>
            <span>+1 234 567 890</span>
          </div>
          <div className="footer-copy">© 2026 ERPSuite. All rights reserved.</div>
        </footer>
      </div>
    </div>
  );
}
